body {
  margin: 0px;
  padding: 0px;
  font-family: "Helvetica";
  font-style: regular;
  background-color: #f9f9f9;
  color: #333333; }

.MailPic {
  display: block;
  padding-top: 15%;
  margin-left: auto;
  margin-right: auto;
  opacity: .08; }

.Project {
  font-family: "Helvetica";
  font-style: regular;
  font-size: 14px;
  margin-right: 50px;
  margin-top: 0px;
  margin-left: 30px;
  float: right;
  display: contain;
  text-align: justify;
  position: relative;
  width: auto;
  height: 100%;
  border: 1px solid #c3c3c3;
  max-width: 100%;
  padding: 40px;
  border-radius: 10px;
  border-spacing: 20px 5px;
  color: #333333; }

.rightSide {
  position: absolute;
  left: 340px;
  top: 20px;
  max-width: 75%;
  float: right;
  background-color: #f9f9f9;
  margin-top: 0px; }

h5 {
  font-size: 19px;
  padding: 0px;
  margin: 0px 0px 10px 0px; }

.titleMain {
  font-size: 16px; }

.subjectMain {
  margin-left: 50px; }

.dateMain {
  float: right;
  margin-right: 10px;
  font-size: 12px; }

.fromMain {
  color: #4990E2; }

.mainIcons {
  float: right;
  margin-right: 80px;
  font-size: 19px; }

.mainIconTrash {
  margin-left: 10px; }

.mainIconSpam {
  margin-left: 10px; }

.mailLink {
  color: #4990E2; }

.trash {
  display: none; }



